import React from "react";
import { Routes, Route, Outlet } from "react-router-dom";
/// Css
import "./index.css";
import "./chart.css";
import "./step.css";

/// Layout
import Nav from "./layouts/nav";

/// Pages
import Error404 from "./common/Error404";

//Scroll To Top
import ScrollToTop from "./layouts/ScrollToTop";

import Admins from "./pages/Admin";
import AddAdmin from "./pages/Admin/AddAdmin";
import Permission from "./pages/CustomRules";
import Profile from "./pages/Profile";
import Projects from "./pages/Projects";
import Employees from "./pages/Employees";
import Departments from "./pages/Departments";
import Assets from "./pages/Assets";
import Deduction from "./pages/Deduction";
import Dashboard from "./pages/Dashboard";
import Attendance from "./pages/Attendance";
import EmployeeProfile from "./pages/Employees/EmployeeProfile";
import Notification from "./pages/Notification";
import Holiday from "./pages/Holiday";
import Organization from "./pages/Organization";
import AddOrganization from "./pages/Organization/AddOrganization";
import CRM from "./pages/CRM";
import Allowances from "./pages/Allowances";

const Markup = () => {
  const allroutes = [
    // Dashboard
    { url: "/", component: <Dashboard /> },

    // Admins
    { url: "/admins", component: <Admins /> },
    { url: "add-admins", component: <AddAdmin /> },
    { url: "edit-admin/:id/:name", component: <AddAdmin /> },

    // Rules
    { url: "rules", component: <Permission /> },
    { url: "rules/:id", component: <Permission /> },

    // Assets
    { url: "assets", component: <Assets /> },

    // Attendance
    { url: "attendance", component: <Attendance /> },

    // Employees
    { url: "employees", component: <Employees /> },
    { url: "employees/profile/:id", component: <EmployeeProfile /> },

    // Holiday
    { url: "holiday", component: <Holiday /> },

    // Projects
    { url: "projects", component: <Projects /> },

    // Departments
    { url: "departments", component: <Departments /> },

    // Deduction
    { url: "deduction", component: <Deduction /> },

    // Profile
    { url: "profile", component: <Profile /> },

    // Allowances
    { url: "allowances", component: <Allowances /> },

    // Notification
    { url: "notification", component: <Notification /> },

    // CRM
    { url: "crm", component: <CRM /> },

    // organization
    { url: "organization", component: <Organization /> },
    { url: "organization/add-organization", component: <AddOrganization /> },
    { url: "organization/edit-organization", component: <AddOrganization /> },
    { url: "organization/view-organization", component: <AddOrganization /> },

    // Error
    { url: "*", component: <Error404 /> },
  ];

  return (
    <>
      <Routes>
        <Route path="page-error-404" element={<Error404 />} />
        <Route element={<MainLayout />}>
          {allroutes.map((data, i) => (
            <Route
              key={i}
              exact
              path={`${data.url}`}
              element={data.component}
            />
          ))}
        </Route>
      </Routes>
      <ScrollToTop />
    </>
  );
};

function MainLayout() {
  return (
    <div id="main-wrapper" className={`show `}>
      <Nav />
      <div
        className="content-body"
        style={{ minHeight: window.screen.height - 45 }}
      >
        <div className="container-fluid">
          <Outlet />
        </div>
      </div>
    </div>
  );
}

export default Markup;
